import type { Metadata } from "next";
import Link from "next/link";
import { DATA } from "@/lib/data";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

export const metadata: Metadata = {
  title: "Tools â€” ToolDrop AI",
  description: "Browse AI tools by category and newest updates.",
};

export default async function ToolsIndexPage() {
  const tools = [...DATA.tools].sort(
    (a, b) =>
      new Date(b.updatedAtISO).getTime() - new Date(a.updatedAtISO).getTime()
  );

  return (
    <div className="mx-auto max-w-6xl px-4 py-10">
      <h1 className="text-3xl font-semibold">Tools</h1>
      <p className="mt-2 text-muted-foreground">
        A curated list of tools people actually use.
      </p>

      <div className="mt-6 grid gap-4 md:grid-cols-2">
        {tools.map((t) => (
          <Link key={t.id} href={`/tools/${t.slug}`}>
            <Card className="rounded-2xl hover:bg-muted/40 transition">
              <CardContent className="p-5">
                <div className="flex items-center gap-2 flex-wrap">
                  {t.tags.slice(0, 3).map((tag) => (
                    <Badge
                      key={tag}
                      variant="secondary"
                      className="rounded-full"
                    >
                      {tag}
                    </Badge>
                  ))}
                </div>

                <div className="mt-2 font-semibold">{t.name}</div>
                <div className="mt-1 text-sm text-muted-foreground">
                  {t.oneLiner}
                </div>
              </CardContent>
            </Card>
          </Link>
        ))}
      </div>
    </div>
  );
}
